import signIn from './signIn.vue'
import signUp from './signUp.vue'
export default {
	components: {signIn,signUp},
	data() {
		return {
			showSignIn:true,
			formType:'1'
		};
	},
	methods: {
		handleSignIn(formType){
			this.formType = formType;
			this.showSignIn = false;
		},
		handleSignUp(){
			this.showSignIn = true;
		}
	}
};
